	onLoad() {
		uni.getSystemInfo({
		    success: (res)=> {
		        let height = res.windowHeight - uni.upx2px(100);
				// console.log(height)
				this.swipHeight = height;
		    }
		});
	},
	methods: {
		//监听原生导航栏按钮点击事件
		onNavigationBarButtonTap(e){
			if(e.index == 1){
				uni.navigateTo({
					url:"../publish/publish"
				})
			}
		},
		//监听原生标题栏搜索框点击事件
		onNavigationBarSearchInputClicked(){
			uni.navigateTo({
				url: '../search/search'
			})
		},
		handleChange(index){
			this.tabIndex = index;
		},
		changeSwipe(e){
			this.tabIndex = e.detail.current
		},
		onReflash(index1){
